
import React from "react";
import { Pane, Heading, Button, Paragraph, Link } from 'evergreen-ui';

// import Project from "./Project";

// Icons
import { InlineIcon } from '@iconify/react';
import boxIcon from '@iconify/icons-feather/box';

import projects from './ProjectsStore'

export default class Projects extends React.Component {

  render() {
    return (
      <Pane
        flexWrap="wrap"
        wordWrap="break-word"
        padding={16}
      >

        <Pane>
          <Heading size={800} margin="default"><InlineIcon icon={boxIcon} width={24} height={24} /> Projects</Heading>
        </Pane>

        <Pane
          display="flex"
          flexWrap="wrap"
          justifyContent="center"
          alignItems="center"
          padding={16}
        >
          {projects.map(project => {
            return (
              <Pane clearfix>
                <Pane
                  elevation={1}
                  float="left"
                  background="tint2"
                  height="15em"
                  width="15em"
                  margin={24}
                  display="flex"
                  justifyContent="center"
                  alignItems="center"
                  flexDirection="column"
                >
                  <Paragraph>
                    <Heading margin={16} size={600}>{project.title}</Heading>
                  </Paragraph>
                  <Paragraph>
                    {project.badges.map(badge => {
                      let img = <img src={badge.img} alt={badge.alt} />
                      return (
                        badge.link ?
                          <Link margin={4} href={badge.link}>
                            {img}
                          </Link>
                          : img)
                    })}
                  </Paragraph>
                  <Paragraph>
                    {project.links.map(link => <Button appearance="minimal" is="a" href={link.url} margin={16} height={32}><InlineIcon height={20} icon={link.icon} /></Button>)}
                  </Paragraph>
                </Pane>
              </Pane>
            )
          })}
        </Pane>
      </Pane>
    )
  }
}